
*{
	margin: 0;
	padding: 0;
}
body{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}
table{
	border-collapse: collapse;
	text-align: center;

	/*vertical-align: center;*/
}
td,th {
	border: 1px solid #bcbcbc;
	padding: 5px 10px;
	width: 120px;
	height: 50px;
}
#app{
	margin-top: 100px;
	position: relative;
}
.title{
	padding: 10px;
	height: 50px;
	color: white;
	background-color: #0090d3;
	text-align: center;
	line-height: 50px;
	margin-bottom: 25px;
}
button{
	background-color:#0090d3;
	color:white;
	border-radius:10px;
	width: 80px;
	height: 40px;
	outline: none;
}
.dialog{
	width: 500px;
	height: 300px;
	position: absolute;
	top: -35px;
	left: 20%;
	background-color: white;
	border:1px solid #bcbcbc;
	border-radius: 10px;
	z-index: 50;
}
.content,.footer{
	text-align:center;
	margin-top: 10px;
}
.content label{
	padding-right: 1rem;
}
.content select{
	width: 173px;
	text-align: center;
}
.footer button{
	margin:0 16px;
}
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    z-index: 20;
}



